// components/Status.tsx
type StatusType = { id: number; text: string; time: string };

interface Props {
  statuses: StatusType[];
}

import styles from "../styles/Status.module.css";

export default function Status({ statuses }: Props) {
  return (
    <>
      {statuses.map((status) => (
        <div key={status.id} className={styles.statusCard}>
          <p>{status.text}</p>
          <span className={styles.statusTime}>{status.time}</span>
        </div>
      ))}
    </>
  );
}
