// pages/status.tsx
import { useState } from "react";
import Status from "../components/Status";
import styles from "../styles/Status.module.css";

export default function StatusPage() {
  const [statuses, setStatuses] = useState<
    { id: number; text: string; time: string }[]
  >([]);
  const [newStatus, setNewStatus] = useState("");

  const handlePost = (e: React.FormEvent) => {
    e.preventDefault();
    if (!newStatus.trim()) return;

    setStatuses([
      { id: Date.now(), text: newStatus, time: new Date().toLocaleString() },
      ...statuses,
    ]);
    setNewStatus("");
  };

  return (
    <div className={styles.statusPage}>
      <form onSubmit={handlePost} className={styles.statusForm}>
        <textarea
          placeholder="Escreva atÃ© 140 caracteres..."
          maxLength={140}
          value={newStatus}
          onChange={(e) => setNewStatus(e.target.value)}
          rows={3}
          className={styles.statusInput}
        />
        <button type="submit" className={styles.statusButton}>
          Publicar
        </button>
      </form>

      <div className={styles.statusFeed}>
        <Status statuses={statuses} />
      </div>
    </div>
  );
}
